.worksBlock {
   height: auto;
   background: #ffff;
   padding-bottom: 2rem;

   .worksContainer {
      display: flex;
      flex-direction: column;
      justify-content: flex-start;

      .works {
         display: flex;
         flex-wrap: wrap;
         justify-content: space-around;
         width: 100%;
         @media screen and (max-width: 1149px) {
            align-items: center;
            flex-direction: column;
            justify-content: center;
            &:last-child {
               margin-bottom: 0;
            }
         }
      }
   }
}
