.workBlock {
   display: flex;
   flex-direction: column;
   height: min-content;
   max-width: 450px;
   padding-bottom: 25px;
   background: #f4f4f4;

   .projectImg {
      display: flex;
      align-items: center;
      justify-content: center;
      background-size: cover;
      /*background-position: center;*/
      width: 100%;
      height: 270px;
      position: relative;

      &::before {
         position: absolute;
         content: '';
         background-color: black;
         top: 0;
         bottom: 0;
         left: 0;
         right: 0;
         opacity: 0.4;
      }

      .workBtn {
         color: #FFF;
         cursor: pointer;
         outline: 0;
         background: #000;
         border: 1px solid #000;
         text-transform: uppercase;
         border-radius: 0;
         text-shadow: none;
         padding: 10px 12px 12px 12px;
         display: inline-block;
         font-weight: 400;
         line-height: 1.25;
         text-align: center;
         white-space: nowrap;
         vertical-align: middle;
         transition: all .2s ease-in-out;
         user-select: none;
         position: relative;
         z-index: 1;

         &:hover {
            background: #ffff;
            color: #000;
         }
      }
   }

   .description {
      display: flex;
      flex-direction: column;
      justify-content: flex-start;

      padding: 5px 20px;

      h3, p {
         margin: 0;
      }
   }

   @media screen and (max-width: 1149px) {
      margin-bottom: 15px;
   }
}
