.mainBlock {
	height: 100vh;
	padding: 50px 0;

	background-position: center;
	background-size: cover;
	background-color: #292b2c;
}

.greeting {
	width: auto;
	text-align: center;

	/*background-color: #c3c3c3;*/
}

.about {
	font-size: 1.5rem;
	text-align: center;
	color: #fff;
	text-shadow: 0px 1px 2px #53535f;
}

.photo {
	width: 300px;
	height: 400px;

	background-color: #e72020;
}

.mainBtn {
	color: #FFF;
	cursor: pointer;
	outline: 0;
	background: #000;
	border: 1px solid #f6f6f6;
	text-transform: uppercase;
	border-radius: 0;
	text-shadow: none;
	padding: 10px 12px 12px 12px;
	font-weight: 400;
	line-height: 1.25;
	text-align: center;
	white-space: nowrap;
	vertical-align: middle;
	transition: all .2s ease-in-out;
	user-select: none;
	opacity: 1;
	margin-top: 10px;
}

.mainBtn:hover {
	color: black;
	background: #ffffff;
}

@media screen and (max-width: 891px) {
	.greeting {
		text-align: center;
	}

	.photo {
		margin: 10px 0;
	}
}
